import React, { Component } from 'react';
import {Popup, TextArea, TransitionablePortal, Segment, Header, Divider, Button} from 'semantic-ui-react';

class EmailComposer extends Component {
    state={text:""}
    onFocus = () => {
        this.setState({text:this.text()})
    }
    
    text = () => {
        return "Shop stock for " + (new Date()).toLocaleDateString('en-AU', {weekday:'long', day:'numeric'}) + "\n\n" +
            this.props.items.map((item, index) => (item.qty + " x " + item.unit + " " + item.name + (item.notes ? " (" + item.notes + ")" : "")))
            .join("\n")
    }
    focusUsernameInputField = input => {
        if (input) {
          setTimeout(() => input.focus(), 100);
        }
      };
    render() {
        return (
            <TransitionablePortal
                open={this.props.open}
                style={{position:'fixed', top:0, left:0 ,width:'100%'}}
            >
                <Segment 
                    style={{backgroundColor:'#DDD',position:'fixed', height:'100%',padding:10,top:0, left:0,width:'100%', zIndex:10000, borderRadius:0}}                        
                > 
                    <Button basic floated='right' icon='close' onClick={this.props.onClose}/>
                    <Header>Copy and paste into an sms</Header>
                    {/* <Divider/> */}
                    <TextArea ref={this.focusUsernameInputField} 
                        style={{width:'100%',height:'80%',padding:10, borderStyle:'none'}}                        
                        value={this.state.text}
                        onFocus={this.onFocus}
                    />
                </Segment>
            </TransitionablePortal>
        );
    }
} 

export default EmailComposer;



