import React, {Component} from 'react';
import {TransitionablePortal, Grid, Modal, Header, Input, Button, Segment, Container} from 'semantic-ui-react'
import color from '../colors'
const timeoutLength = 2500

class NotesModal extends Component {

    state = { isOpen: false }

    handleOpen = (e) => {
      console.log("pen")
      this.setState({ isOpen: true });
      // this.setState({ isOpen: true },() => setTimeout(() => this.input.focus()),0);
      // e.stopPropagation();  
    //   this.timeout = setTimeout(() => {
    //     this.setState({ isOpen: false })
    //   }, timeoutLength)
    }
  
    handleClose = () => {
      this.setState({ isOpen: false });
      // clearTimeout(this.timeout)
    }
   
    handleKeyPress = (e) => {
      if (e.key === 'Enter') {
        this.handleClose();
        this.props.onNotesSubmit(e);
      }
    }

    stopPropagation = (e) => {
      e.stopPropagation();
    }

    render() {
        return (
          // <Modal basic 
          // style={{width:'100%', position:'fixed', bottom:0, left:0}}
          //   dimmer={false}
          //   size="fullscreen"
          //   trigger={this.props.trigger}
          //   open={this.state.isOpen}
          //   onClose={this.handleClose}
          //   onOpen={this.handleOpen}
          // >
        <TransitionablePortal
        closeOnDocumentClick
            openOnTriggerClick
            trigger={this.props.trigger}
            onClose={this.handleClose}
            onOpen={this.handleOpen}
            transition={{animation:'fade up', duration:300}}
//            closeOnDocumentClick={false}
            style={{width:'100%', position:'absolute', top:0, left:0, zIndex:91000}}
//            open={this.state.isOpen}
          >          
          <div
            style={{width:'100%', height:'100%', borderRadius:0,backgroundColor:'#0000', position:'fixed', top:62, left:0, zIndex:5000 }}
            onClick={this.handleClose}
          >
            <Modal.Content>
              <Modal.Description>
                <Segment textAlign="center" raised  inverted basic className="black"
                  style={{width:'100%', height:'100%', position:'absolute', top:0, left:0, backgroundColor:'transparent',zIndex:5000}}
                >
                  <Grid verticalAlign="middle">
                    <Grid.Column width={3}>
                      <Button className="black" icon="chevron left"/>
                    </Grid.Column>
                    <Grid.Column width={10}>
                      <Header 
                          style={{color:color(this.props.item.tags)}}
                      >
                        {this.props.item.name}
                      </Header>
                      <Header inverted>{this.props.item.qty + " " + this.props.item.unit}</Header>
                    </Grid.Column>
                    <Grid.Column width={3}>
                      <Button className="black" icon="chevron right"/>
                    </Grid.Column>
                  </Grid>
                  <Container textAlign="left">
                  <Input inverted  size="massive" ref={(r) => this.input = r} className="red" className="transparent"
                    // style={{color:color(this.props.item.tags)}}
                    style={{width:'100%'}}
                    tabIndex={1}
                    placeholder="Notes"
                    defaultValue={this.props.item.notes}
                    onKeyPress={this.handleKeyPress}
                    onClick={this.stopPropagation}
                  />
                  </Container>
                </Segment>
              </Modal.Description>
            </Modal.Content>
            </div>
          </TransitionablePortal>          
        )
    }
}

export default NotesModal;




